import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

const Header = ({ siteTitle }) => (
  <header>
    <nav className="header-nav">
      <Link to="/" className="header-title"><h1>{siteTitle}</h1></Link>
      <ul className="nav-items">
        <li>
          <Link
            to="/#about"
            getProps={({ location }) => location.hash === "#about" ? { className: "active" } : null}
          >
            About
          </Link>
        </li>
        <li>
          <Link
            to="/#services"
            getProps={({ location }) => location.hash === "#services" ? { className: "active" } : null}
          >
            Services
          </Link>
        </li>
        <li>
          <Link
            to="/#contact"
            getProps={({ location }) => location.hash === "#contact" ? { className: "active" } : null}
          >
            Contact
          </Link>
        </li>
      </ul>
    </nav>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
