import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faGithubSquare, faTwitterSquare, faLinkedin } from "@fortawesome/free-brands-svg-icons"

import Header from "./header"
import "../styles/layout.css"

const Layout = ({ children }) => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
      }
    `}
    render={data => (
      <>
        <Header siteTitle={data.site.siteMetadata.title} />
        <div>
          <main>{children}</main>
          <footer>
            © {new Date().getFullYear()}, Ruben Karapetyan
            <div className="social-icons">
              <a href="https://github.com/roubkar" rel="noopener noreferrer" target="_blank">
                <FontAwesomeIcon icon={faGithubSquare} size="lg" />
              </a>
              <a href="https://twitter.com/roubkar" rel="noopener noreferrer" target="_blank">
                <FontAwesomeIcon icon={faTwitterSquare} size="lg" />
              </a>
              <a href="https://www.linkedin.com/in/rubenkarapetyan" rel="noopener noreferrer" target="_blank">
                <FontAwesomeIcon icon={faLinkedin} size="lg" />
              </a>
            </div>
          </footer>
        </div>
      </>
    )}
  />
)

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
